.contact-container {
    width:100%;
    max-width:1300px;
    margin:auto;
    height:820px;
}

@media only screen and (max-width: 1250px) {
    .contact-container {
        width:100%;
        margin:auto;
        height:1400px;
        padding:0px;
        justify-content: center;
        overflow: hidden;
    }
}